.placeBox .container {
    display: flex;
    width: 100%;
    height: 190px;
    padding-bottom: 16px;
    padding-right: 16px;
    cursor: default;
}

.placeBox .TopPlaceContainer {
    display: flex;
    width: 100%;
    height: 130px;   
    padding-bottom: 16px;
    padding-right: 16px;
    align-self: center;
    cursor: default;
}

.placeBox .TextBox {
    background-color: #F8F8F8;
    display: flex;
    flex-direction: column;
    width: 308px;
    padding: 36px 16px;
}

.placeBox .RankTop {
    color: #FFC300;
    font-size: 17px;
    font-weight: 800;
}

.placeBox .RankReg {
    color: #5863F8;
    font-size: 17px;
    font-weight: 800;
}

.placeBox .PlaceName {
    font-size: 19px;
    font-weight: 600;
    font-family: 'Kanit';
    color: #0c0c0c;
    line-height: 2;
    padding-top: 6px;
    padding-bottom: 2px;
    cursor: pointer;
}

.placeBox .PlaceDesc {
    font-size: 14px;
    font-family: 'Kanit';
    color: #B1B1B1;
}

.placeBox .NextButton {
    width: 60px;
    height: 100%;
    background-color: #5863F8;
    color: white;
    border: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

